import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {MatStepperModule} from '@angular/material/stepper'; 
import {MatCardModule} from '@angular/material/card';
import {MatGridListModule} from '@angular/material/grid-list'; 
import {MatButtonModule} from '@angular/material/button';
import {MatDividerModule} from '@angular/material/divider'; 
import {STEPPER_GLOBAL_OPTIONS} from '@angular/cdk/stepper';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { FlexLayoutModule } from '@angular/flex-layout';

import { HeaderComponent } from './layout/header/header.component';
import { DataSourceComponent } from './data-source/data-source.component';
import { DataStreamComponent } from './data-stream/data-stream.component';
import { DataMartComponent } from './data-mart/data-mart.component';
import { DataAnalyticsComponent } from './data-analytics/data-analytics.component';
import { ReportViewComponent } from './report-view/report-view.component';
import { DataTransformerComponent } from './data-transformer/data-transformer.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    DataSourceComponent,
    DataStreamComponent,
    DataMartComponent,
    DataAnalyticsComponent,
    ReportViewComponent,
    DataTransformerComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    NgxChartsModule,
    FontAwesomeModule,
    FlexLayoutModule,
    BrowserAnimationsModule,
    MatStepperModule,
    MatCardModule,
    MatGridListModule,
    MatButtonModule,
    MatDividerModule
  ],
  providers: [
    {
      provide: STEPPER_GLOBAL_OPTIONS,
      useValue: { displayDefaultIndicatorType: false }
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
